import React from "react"
import Layout from "../components/layout"
import { Link } from "gatsby"
import SEO from "../components/seo";

const posts = [
  {
    title: 'Técnicas e ferramentas simples de controle financeiro pessoal para você começar a aplicar hoje.',
    author: 'Mannuel Victor',
    link: '/blog/tecnicas-simples-controle-financeiro-pessoal',
    readingTime: '5 min.',
    date: '2021-01-03T03:00:00'
  },
];

export default function Home() {
  return (
    <Layout>
      <SEO />
      <div className="text-5xl mt-14 mb-5">Dicas e Ferramentas para você triunfar em suas finanças.</div>
      <div className="leading-relaxed text-xl text-gray-600">
        No Finança Elementar irei repassar todo o conhecimento adquirido por experiência própria e também as técnicas e ferramentas que ajudaram outras pessoas a melhorarem o controle sobre suas próprias finanças.
      </div>
      <div className="leading-relaxed mt-3 text-xl text-gray-600">
        Sugestões são bem vindas, ou para qualquer outro assunto, fale comigo pelo
         e-mail <Link to="mailto:financaelementar@gmail.com" className="font-semibold">financaelementar@gmail.com</Link>.
      </div>
      <div className="border-t mt-10 mb-10"></div>
      <div className="mb-3">
        {posts.map((element, index) => (
          <div key={index}>
            <Link className="flex flex-col border-l-4 pl-3 mt-5 py-3" to={element.link}>
              <div className="flex-1 flex justify-start font-bold text-3xl">
                {element.title}
              </div>
              <div className="flex-1 flex justify-start items-center text-gray-500 mt-2">
                <div className="mr-2 ml-1 h-0.5 w-6 bg-gray-400"></div>
                <div className="flex items-center">
                  <div>
                    {element.author}
                  </div>
                  <div className="border-l ml-2 pl-2">
                    {new Date(element.date).toLocaleDateString('pt-BR')}
                  </div>
                  <div className="border-l ml-2 pl-2">
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"></path></svg>
                  </div>
                  <div className="ml-1">
                  {element.readingTime}
                  </div>
                </div>
              </div>
            </Link>
          </div>
        ))}
      </div>
    </Layout>
  )
}
